
.hidden {
  visibility: hidden;
}
.head {
  padding-left: 10px;
  height: 50px;
  background-color: #fff;
  color: #333;
  line-height: 50px;
  font-size: 14px;
}
.head .back-btn {
  display: inline-block;
  height: 100%;
}
.bank-list .bank {
  border-top: 1px solid #ddd;
  height: 40px;
}
.bank-list .bank .logo {
  vertical-align: top;
  line-height: 40px;
}
.bank-list .bank .bank-name {
  width: 35%;
  line-height: 40px;
}
.bank-list .bank .card-type {
  width: 25%;
  vertical-align: top;
  line-height: 40px;
}



